body {
  margin: 1em;
  padding: 0;
  background-color: #fff;
}

.cssgrid {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 12px;
  grid-auto-columns: minmax(100px, auto);
}

.stop-placard {
  grid-column: 1 / 3;
  grid-row: 1 / 2;
}

.olli-logo-div {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}

.stop-name-div {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
}

.arrival-list, .stop-info, .clock-weather {
  grid-column: 1 / 2;
}

.mapboxgl-map {
  grid-column: 2 / 3;
  grid-row: 2 / 6;
}

.stop-panel .box-title {
  font-size: 18pt;
  text-align: center;
  padding-bottom: 8px;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
}

h1 {
  font-size: 48pt;
}

.stop-placard, .olli-logo-div, .stop-name-div {
  color: white;
  background-color: #046b99;
  padding: 12px;
}
.stop-placard>h1 {
  text-align: left;
}

button.bx--btn--secondary {
  display: -ms-inline-flexbox;
  display: inline-flex;
  background-color: white;
  border-color: #046b99;
  border-radius: 8px;
  margin: 2px 4px;
}
button>img {
  padding-left: 4px;
}

button.btn--bigaction {
  height: 88px;
  border-width: 4px;
  border-radius: 50%;
}
button.btn--bigaction:focus {
  outline: unset;
  background-color: rgba(255, 102, 102, 0.5);
  border-width: 6px;
}

.credits-small {
  font-size: 10pt;
}

.convo, .progresswin {
  background-color: white;
  padding: 6px;
  text-align: left;
}

.convo {
  width: 100%;
  height: 100%;
  color: #444;
}

.convo h1,.convo h2, .convo h3, .convo h4 {
  color: #444;
  text-align: left;
  font-weight: 700;
}
.convo ol {
  list-style: decimal inside;
  line-height: normal;
}

.instructions {
  font-weight: 300;
  font-size: 10pt;
  font-style: italic;
  float: left;
  color: white;
  padding: 12px;
  vertical-align: middle;
}

.mapboxgl-popup-content {
  background-color: coral;
  font-size: 18pt;
  border-radius: 18px;
}

.protoui {
  border: 1px solid white;
}

.weather img {
  height: 75px;
  width: 75px;
  position: relative;
  top: -12px;
}

.weather .forecast {
  vertical-align: top;
}

/* .weather .forecast > span {
  display: block;
  font-size: 14px;
  line-height: 18px;
} */

.forecast table {
  width: 100%;
}
tr.small {
  font-size: 75%;
}
.forecast td {
  vertical-align: top;
}

.olli-logo {
  height: 36px;
  padding-right: 72px;
}

.stop-picture {
  width: 386px;
}

.stop-info {
  padding-bottom: 12px;
  border-top: 4px solid #b3efff;
  border-bottom: 2px solid #b3efff;
}

.stop-info h2 {
  padding: 8px 0;
}



